import './App.css';
import React from 'react';
import axios from 'axios';

class App extends React.Component{
  constructor(){
    super()
    this.state = {
      reports: [],
      offset: 1
    }
  }

  componentDidMount(){
    axios.get('https://nanaimg.net/reports')
      .then(res => {
        this.setState({
          ...this.state,
          reports: [...res.data.reports]
        })
      })
      .catch(err => {
        console.log("An error occurred");
      })
  }

  goToPost = (e) => {
    window.open(`https://nanaimg.net/image/${e.target.id.split('rb')[1]}`);
  }

  goToComment = (e) => {
    window.open(`https://nanaimg.net/comments/${e.target.id.split('rc')[1]}`);
  }

  reportRemoveComment = (e) => {
    axios.delete(`https://nanaimg.net/reports/comments/${e.target.id.split('-')[1]}`)
      .then(res => {
        axios.get('https://nanaimg.net/reports')
        .then(res => {
          this.setState({
            ...this.state,
            reports: [...res.data.reports]
          })
        })
        .catch(err => {
          console.log("An error occurred");
        })
      })
      .catch (err => {
        console.log("an error occurred")
      })
  }

  reportRemovePost = (e) => {
    axios.delete(`https://nanaimg.net/reports/posts/${e.target.id.split('-')[1]}`)
      .then(res => {
        axios.get('https://nanaimg.net/reports')
        .then(res => {
          this.setState({
            ...this.state,
            reports: [...res.data.reports]
          })
        })
        .catch(err => {
          console.log("An error occurred");
        })
      })
      .catch (err => {
        console.log("an error occurred")
      })
  }

  commentRemove = (e) => {
    let fd = new FormData();
    if (document.querySelector(`#crr${e.target.id.split('-')[1]}`).textContent !== "other"){
      fd.append('reason', document.querySelector(`#crr${e.target.id.split('-')[1]}`).textContent);
    } else {
      if (document.querySelector(`#crt${e.target.id.split('-')[1]}`).textContent.length > 0){
        fd.append('reason', document.querySelector(`#crt${e.target.id.split('-')[1]}`).textContent);
      } else {
        fd.append('reason', "other");
      }
    }
    axios.post(`https://nanaimg.net/reports/comment/remove/${e.target.id.split('-')[1]}`, fd)
      .then(res => {
        axios.get('https://nanaimg.net/reports')
        .then(res => {
          this.setState({
            ...this.state,
            reports: [...res.data.reports]
          })
        })
        .catch(err => {
          console.log("An error occurred");
        })
      })
      .catch (err => {
        console.log("an error occurred")
      })
  }

  postRemove = (e) => {
    let fd = new FormData();
    if (document.querySelector(`#prr${e.target.id.split('-')[1]}`).textContent !== "other"){
      fd.append('reason', document.querySelector(`#prr${e.target.id.split('-')[1]}`).textContent);
    } else {
      if (document.querySelector(`#prt${e.target.id.split('-')[1]}`).textContent.length > 0){
        fd.append('reason', document.querySelector(`#prt${e.target.id.split('-')[1]}`).textContent);
      } else {
        fd.append('reason', "other");
      }
    }
    axios.post(`https://nanaimg.net/reports/post/remove/${e.target.id.split('-')[1]}`, fd)
      .then(res => {
        axios.get('https://nanaimg.net/reports')
        .then(res => {
          this.setState({
            ...this.state,
            reports: [...res.data.reports]
          })
        })
        .catch(err => {
          console.log("An error occurred");
        })
      })
      .catch (err => {
        console.log("an error occurred")
      })
  }

  manifestoRemove = (e) => {
    let fd = new FormData();
    if (document.querySelector(`#prr${e.target.id.split('-')[1]}`).textContent !== "other"){
      fd.append('reason', document.querySelector(`#prr${e.target.id.split('-')[1]}`).textContent);
    } else {
      if (document.querySelector(`#prt${e.target.id.split('-')[1]}`).textContent.length > 0){
        fd.append('reason', document.querySelector(`#prt${e.target.id.split('-')[1]}`).textContent);
      } else {
        fd.append('reason', "other");
      }
    }
    axios.post(`https://nanaimg.net/reports/manifesto/remove/${e.target.id.split('-')[1]}`, fd)
      .then(res => {
        axios.get('https://nanaimg.net/reports')
        .then(res => {
          this.setState({
            ...this.state,
            reports: [...res.data.reports]
          })
        })
        .catch(err => {
          console.log("An error occurred");
        })
      })
      .catch (err => {
        console.log("an error occurred")
      })
  }

  clickNext = () => {
    axios.get(`https://nanaimg.net/reports/${this.state.offset + 1}`)
      .then(res => {
        this.setState({
          ...this.state,
          reports: [...res.data.reports],
          offset: this.state.offset + 1
        })
      })
      .catch(err => {
        console.log("An error occurred");
      })
  }

  clickPrev = () => {
    axios.get(`https://nanaimg.net/reports/${this.state.offset - 1}`)
      .then(res => {
        this.setState({
          ...this.state,
          reports: [...res.data.reports],
          offset: this.state.offset - 1
        })
      })
      .catch(err => {
        console.log("An error occurred");
      })
  }

  render(){
    return(
      <>
        <h1 id="h1-report-queue">Report Queue</h1>
        <div id="report-nav-buttons">
          {(this.state.offset > 1) ? <button onClick={this.clickPrev} className="buttons-report-queue-nav">Prev Page</button> : <></>}
          {(this.state.reports.length > 49) ? <button onClick={this.clickNext} className="buttons-report-queue-nav">Next Page</button> : <></>}
        </div>
        <div id="report-queue-heading">
          <h2 className="report-queue-types rep-type">Type</h2>
          <h2 className="report-queue-types rep-num">ID</h2>
          <h2 className="report-queue-types rep-reas">Report Reason</h2>
          <h2 className="report-queue-types rep-auth">Post Author</h2>
          <h2 className="report-queue-types rep-date">Post Date</h2>
          <h2 className="report-queue-types rep-post">Post Text</h2>
          <h2 className="report-queue-types rep-rep">Report Text</h2>
          <h2 className="report-queue-types rep-act">Actions</h2>
        </div>
        <div id="div-report-actions"></div>
        {this.state.reports.map((report) => {
          if (report.post_type === 'post'){
            return (
              <div key={report.report_id} className="report-queue-cards" id={`post-report${report.post_number}`}>
                <h2 className="report-queue-data rep-type">{report.post_type}</h2>
                <h2 className="report-queue-data rep-num">{report.post_number}</h2>
                <h2 id={`prr${report.post_number}`} className="report-queue-data rep-reas">{report.report_reason}</h2>
                <h2 className="report-queue-data rep-auth">{report.post_name}</h2>
                <h2 className="report-queue-data rep-date">{report.post_date}</h2>
                <h2 className="report-queue-data rep-post">{report.post_text}</h2>
                <h2 id={`prt${report.post_number}`} className="report-queue-data rep-rep">{report.report_text}</h2>
                  <div className="actions rep-act">
                    <button id={`remi-${report.post_number}`} className="button-report-queue" onClick={this.postRemove}>Remove Post</button>
                    <button id={`remm-${report.post_number}`} onClick={this.manifestoRemove} className="button-report-queue">Remove Manifesto</button>
                    <button id={`remrc-${report.post_number}`} className="button-report-queue" onClick={this.reportRemovePost}>Remove Report</button>
                    <button id={`rb${report.post_number}`} className="button-report-queue" onClick={this.goToPost}>Go to Post</button>
                  </div>
              </div>
            )
          } else {
            return (
              <div key={report.report_id} className={`report-queue-cards comment-report${report.post_number}`}>
                <h2 className="report-queue-data rep-type">{report.post_type}</h2>
                <h2 className="report-queue-data rep-num">{report.post_number}</h2>
                <h2 id={`crr${report.post_number}`} className="report-queue-data rep-reas">{report.report_reason}</h2>
                <h2 className="report-queue-data rep-auth">{report.post_name}</h2>
                <h2 className="report-queue-data rep-date">{report.post_date}</h2>
                <h2 className="report-queue-data rep-post">{report.post_text}</h2>
                <h2 id={`crt${report.post_number}`} className="report-queue-data rep-rep">{report.report_text}</h2>
                <div className="actions">
                  <button id={`remc-${report.post_number}`} className="button-report-queue" onClick={this.commentRemove}>Remove Comment</button>
                  <button id={`remrc-${report.post_number}`} className="button-report-queue" onClick={this.reportRemoveComment}>Remove Report</button>
                  <button id={`rc${report.post_number}`} className="button-report-queue" onClick={this.goToComment}>Go to Comment</button>
                </div>
              </div>
            )
          }
        })}
      </>
    )
  }
}

export default App;
